<template>
  <section>
    <transition-group name="svganim" appear>
      <v-flex sm12 key="svg">
        <svgicon class="talkinglip" icon="malecartoonzipped" :original="true"></svgicon>
      </v-flex>
  
      <div key="text">
        <h3>How can I help you ?</h3>
      </div>
    </transition-group>
  </section>
</template>

<script>
import "../../assets/compiled-icons/malecartoonzipped";
export default {};
</script>

<style lang="scss" scoped>
section {
  width: 100%;
}

div {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  width: 100%;
  & > h3 {
    font-family: "Berkshire Swash", cursive !important;
    font-size: 2.3rem;
    margin: 10px;
    border-right: 1px solid black;
    width: 0ch;
    white-space: nowrap;
    overflow: hidden;
    animation: typing 5s steps(22, end), blink-caret 1s infinite;
    animation-delay: 2s;
    animation-fill-mode: forwards;
  }
}

@media screen and (max-width: 400px) {
  div > h3 {
    font-size: 1.5rem;
  }
}

@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 16.5ch;
  }
}

@keyframes blink-caret {
  50% {
    border-color: transparent;
  }
}

.svganim-enter-active {
  transition: all 1s 800ms ease-in-out;
}
.svganim-enter {
  opacity: 0;
  transform: translateX(50px);
}
.svganim-enter-to {
  opacity: 1;
  transform: translateX(0);
}
.svg-icon {
  display: inline-block;
  color: inherit;
  vertical-align: middle;
  fill: none;
  stroke: currentColor;
  width: 50%;
}

.svg-fill {
  fill: currentColor;
  stroke: none;
}
</style>

<style>
/* recommended css code for vue-svgicon */

.talkinglip path[pid="22"] {
  fill: rgb(166, 87, 80);
  opacity: 1;
  animation: talking 1.1s 5;
  animation-delay: 2s;
}

.talkinglip path[pid="23"] {
  animation: lipup 1.1s 5;
  animation-delay: 2s;
}

.talkinglip path[pid="21"] {
  animation: lipdown 1.1s 5;
  animation-delay: 2s;
}

@keyframes lipup {
  25% {
    transform: translateY(-2px);
  }
  50% {
    transform: translateY(-5px);
  }
  75% {
    transform: translateY(-2px);
  }
  100% {
    transform: translateY(0px);
  }
}

@keyframes lipdown {
  25% {
    transform: translateY(2px);
  }
  50% {
    transform: translateY(5px);
  }
  75% {
    transform: translateY(2px);
  }
  100% {
    transform: translateY(0px);
  }
}

@keyframes talking {
  25% {
    fill: white;
    opacity: 0.6;
  }
  50% {
    fill: white;
    opacity: 1;
  }
  75% {
    fill: white;
    opacity: 0.8;
  }
  90% {
    fill: white;
    opacity: 0.3;
  }
  100% {
    fill: rgb(166, 87, 80);
    opacity: 0.6;
  }
}
</style>



